.HB{
    width: 100%;
    height: 100%;   
    /* padding: 0 5%;  */
}
.BG-P{
    background: #0b96f3;
    color: #ffffff;
}
.GP{
    padding: 30px;
} 
.GPH{
    width: fit-content;
    height: 500px;
    overflow: hidden;
}
.CT{
    font-size: 36px;
    font-weight: 700;
    margin-top: 0;
    margin-bottom: 11.5px;
}
.CD p{
    margin: 0 11.5px;
    font-family: 'Raleway', sans-serif;
    font-size: 16px;
    line-height: 1.7142857;
}
.FL{
    font-family: 'Raleway', sans-serif;
    font-size: 14px;
    line-height: 1.7142857;
    color: #fff;
}
.grid-1x2 {    
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: #fff;
}



.image {
    z-index: 1;
    opacity: 1;
    display: block;
    width: 100%;
    height: 100%;
    transition: .5s ease;
    backface-visibility: hidden;
  }
  
  .middle {
    transition: .5s ease;
    z-index: 2;
    cursor: pointer;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;    
  }
  
  .container:hover .image {
    opacity: 0.3;
  }
  
  .container:hover .middle {
    opacity: 1;
  }
  
  .text {
    background-color: #0b96f3;
    color: white;
    font-size: 14px;
    padding: 16px 32px;
    border-radius: 5px;
    /* box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22); */
    box-shadow: 0 10px 6px -6px #777;
    text-decoration: underline !important;

  }



.article-img {
    margin: 0;
    position: relative;
    background: transparent;
    overflow: hidden;
}

.triangle-up::before {
    content: "";
    position: absolute;
    /* bottom: 0; */
    right: 0;
    left: auto;
    width: 100%;
    height: 50px;
    /* background: url("./Images/img-mask-up.png") no-repeat right bottom; */
    z-index: 2;
    border: 0;
    -webkit-transition: height 0.2s ease 0s;
    transition: height 0.2s ease 0s;
}
.triangle-down::before {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    left: auto;
    width: 100%;
    height: 50px;
    /* background: url("./Images/img-mask-down.png") no-repeat right bottom; */
    z-index: 2;
    border: 0;
    -webkit-transition: height 0.2s ease 0s;
    -moz-transition: height 0.2s ease 0s;
    transition: height 0.2s ease 0s;
}
.article-img::after {
    content: "";
    background: #000000;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    opacity: 0;
    filter: alpha(opacity=0);
    -webkit-transition: opacity 0.2s ease 0s;
    transition: opacity 0.2s ease 0s;
}
