.headerLogo{
  text-align: center;
  font-size: 20px;
  font-weight: 500;
  width: 20% !important;
}
.headerLogo img{
  width: 50px;
  height: 50px;
}
.headerLogo p{
  font-size: 25px;
}
.headerLogo span{
  font-size: 15px;
}
.main-title{
  font-size: 22px;
  font-weight: 600;
  margin: 0px;
  margin: -5px 0 0 5px;
  color: #0B96F3;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

/* footer */
.footer{
  height: 200px;
  color: #ddd;
}
.footerbody{
  background: #23242a;/*#30336b;*/
  padding: 2% 5%;
}
hr{
	color: #fff;
}
.footerbody h4{
	font-weight: 600;
}
.sm-icons{
	display: flex;
}
.sm-icons svg{
  cursor: pointer;
  color: #fff;
	margin-right: 15px !important;
}
.copyrights{
  text-align: left;
  background: #1c1d22;/*#0B96F3;*/
  height: 20%;
  font-size: 13px;
  padding: 15px 0 0 50px;
}
.copyrightsLink:hover {
	color: #16a085;
}
.footerhead{
  width: fit-content;
  font-size: 14px;
  font-weight: 600;
  margin: 5px 0 5px 0;
  /* border-bottom: 1px solid #eee; */  
}
.footerbodycontent{
  font-size: 14px;
  width: 80%;
  line-height: 1.7142857;
  margin-bottom: 20px;  
}
.footerbodycontentAdd{
  font-size: 14px;
  width: 80%;
}
.list-marked {
	letter-spacing: .075em;
	width: 40%;
	list-style: none;
  margin-left: -20px;  
}
.list-marked a:hover {
	margin-left: 5px;
	color: #16a085 !important;
	text-decoration: underline !important;
}
.list-marked li {
	font-size: 12px;
  margin: 15px;
	color: #fff;
}
.list-marked a {
	text-decoration: none;
	color: #fff;
}
.list-marked li svg{
	font-size: 10px !important;
	margin-right: 5px;	
}



.main-header{
  background-color: #1c1d22 !important;
  color: #0B96F3 !important;
  /* position: fixed !important; */
  /* top: 5% !important; */
}

.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1001;
  top: 0px;
  right: 0;
  display: flex;
  flex-direction: column;
  background-color: #111;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
}

.sidenav button {
  /* padding: 8px 8px 8px 32px; */
  text-decoration: none !important;
  font-size: 25px;
  color: #818181;
  display: block;
  transition: 0.3s;
  width: 50% !important;
  text-align: left;
}

.sidenav a:hover {
  color: #f1f1f1;
}

.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}

@media screen and (max-height: 450px) {
  .sidenav {padding-top: 15px;}
  .sidenav a {font-size: 18px;}
}


.HeaderBtn{
  background: transparent !important;
  color: #fff !important;  
  border-bottom: 1px solid transparent !important;
  margin-left: 10px !important;
  font-size: 12px !important;
  font-weight: 600 !important;
}
.HeaderBtn-active{
  border-bottom: 1px solid #0B96F3 !important;
  color: #0B96F3 !important;
  border-bottom-left-radius: 0px !important;
  border-bottom-right-radius: 0px !important;
}

.small-header{
  position: fixed;
  background: #0B96F3;
  color: #fff;
  top: 0px;
  width: 100%;
  height: 50px;
  z-index: 1100;
  transition: top 0.3s;
}

.small-header-email{
  margin: 10px 0 0 3%;
  float: left;
  font-size: 12px;
  font-family: Arial, Helvetica, sans-serif;
}
.small-header-phone{
  margin: 10px 3% 0 0;
  float: right;
  font-size: 12px;
  font-family: Arial, Helvetica, sans-serif;
}


.body-content{
  margin: 2%;
  padding: 5px;
}
.body-content h5{
  text-align: center;
  font-size: 18px
}
.body-content p{
  font-size: 14px;
}